"use client";

import * as React from "react";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogClose, DialogTrigger } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Book } from "@/lib/models";

interface BookFormProps {
  book?: Book;
  open: boolean;
  onOpenChange: (open: boolean) => void;
  onSubmit: (book: Omit<Book, "id">, id?: number) => void;
}

export default function BookForm({ book, open, onOpenChange, onSubmit }: BookFormProps) {
  const [title, setTitle] = React.useState(book?.title || "");
  const [author, setAuthor] = React.useState(book?.author || "");
  const [genre, setGenre] = React.useState(book?.genre || "");
  const [rating, setRating] = React.useState(book?.rating.toString() || "0");
  const [status, setStatus] = React.useState<Book["status"]>(book?.status || "want to read");
  const [errors, setErrors] = React.useState<{ [key: string]: string }>({});

  React.useEffect(() => {
    setTitle(book?.title || "");
    setAuthor(book?.author || "");
    setGenre(book?.genre || "");
    setRating(book?.rating.toString() || "0");
    setStatus(book?.status || "want to read");
    setErrors({});
  }, [book, open]);

  const validate = () => {
    const newErrors: { [key: string]: string } = {};
    if (!title.trim()) newErrors.title = "Title is required";
    if (!author.trim()) newErrors.author = "Author is required";
    if (!genre.trim()) newErrors.genre = "Genre is required";
    const ratingNum = Number(rating);
    if (isNaN(ratingNum) || ratingNum < 0 || ratingNum > 5) newErrors.rating = "Rating must be a number between 0 and 5";
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = () => {
    if (!validate()) return;
    onSubmit(
      {
        title: title.trim(),
        author: author.trim(),
        genre: genre.trim(),
        rating: Number(rating),
        status,
      },
      book?.id
    );
    onOpenChange(false);
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogTrigger asChild>
        <Button>{book ? "Edit Book" : "Add Book"}</Button>
      </DialogTrigger>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>{book ? "Edit Book" : "Add Book"}</DialogTitle>
        </DialogHeader>
        <div className="grid gap-4 py-4">
          <div>
            <label className="block text-sm font-medium mb-1" htmlFor="title">
              Title
            </label>
            <Input
              id="title"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              aria-invalid={!!errors.title}
              aria-describedby="title-error"
            />
            {errors.title && <p id="title-error" className="text-destructive text-sm mt-1">{errors.title}</p>}
          </div>
          <div>
            <label className="block text-sm font-medium mb-1" htmlFor="author">
              Author
            </label>
            <Input
              id="author"
              value={author}
              onChange={(e) => setAuthor(e.target.value)}
              aria-invalid={!!errors.author}
              aria-describedby="author-error"
            />
            {errors.author && <p id="author-error" className="text-destructive text-sm mt-1">{errors.author}</p>}
          </div>
          <div>
            <label className="block text-sm font-medium mb-1" htmlFor="genre">
              Genre
            </label>
            <Input
              id="genre"
              value={genre}
              onChange={(e) => setGenre(e.target.value)}
              aria-invalid={!!errors.genre}
              aria-describedby="genre-error"
            />
            {errors.genre && <p id="genre-error" className="text-destructive text-sm mt-1">{errors.genre}</p>}
          </div>
          <div>
            <label className="block text-sm font-medium mb-1" htmlFor="rating">
              Rating (0-5)
            </label>
            <Input
              id="rating"
              type="number"
              min={0}
              max={5}
              value={rating}
              onChange={(e) => setRating(e.target.value)}
              aria-invalid={!!errors.rating}
              aria-describedby="rating-error"
            />
            {errors.rating && <p id="rating-error" className="text-destructive text-sm mt-1">{errors.rating}</p>}
          </div>
          <div>
            <label className="block text-sm font-medium mb-1">Status</label>
            <div className="flex gap-4">
              <label className="inline-flex items-center">
                <input
                  type="radio"
                  name="status"
                  value="want to read"
                  checked={status === "want to read"}
                  onChange={() => setStatus("want to read")}
                  className="mr-2"
                />
                Want to Read
              </label>
              <label className="inline-flex items-center">
                <input
                  type="radio"
                  name="status"
                  value="read"
                  checked={status === "read"}
                  onChange={() => setStatus("read")}
                  className="mr-2"
                />
                Read
              </label>
            </div>
          </div>
        </div>
        <DialogFooter className="space-x-2">
          <DialogClose asChild>
            <Button variant="outline">Cancel</Button>
          </DialogClose>
          <Button onClick={handleSubmit}>{book ? "Update" : "Add"}</Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
