"use client";

import React, { useState } from "react";
import { Book } from "@/lib/models";
import BookItem from "./BookItem";
import { Table, TableHeader, TableBody, TableRow, TableHead, TableCell } from "@/components/ui/table";

interface BookListProps {
  books: Book[];
  onEdit: (book: Book) => void;
  onDelete: (id: number) => void;
  onToggleStatus: (id: number) => void;
}

export default function BookList({ books, onEdit, onDelete, onToggleStatus }: BookListProps) {
  const [sortBy, setSortBy] = useState<"author" | "genre" | "">("");

  const sortedBooks = React.useMemo(() => {
    if (!sortBy) return books;
    return [...books].sort((a, b) => a[sortBy].localeCompare(b[sortBy]));
  }, [books, sortBy]);

  return (
    <div>
      <div className="mb-4 flex items-center gap-4">
        <label htmlFor="sort" className="font-medium">
          Sort by:
        </label>
        <select
          id="sort"
          value={sortBy}
          onChange={(e) => setSortBy(e.target.value as "author" | "genre" | "")}
          className="rounded border border-input bg-background px-3 py-1 text-sm"
        >
          <option value="">None</option>
          <option value="author">Author</option>
          <option value="genre">Genre</option>
        </select>
      </div>
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>Title</TableHead>
            <TableHead>Author</TableHead>
            <TableHead>Genre</TableHead>
            <TableHead>Rating</TableHead>
            <TableHead>Status</TableHead>
            <TableHead>Actions</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {sortedBooks.map((book) => (
            <BookItem
              key={book.id}
              book={book}
              onEdit={onEdit}
              onDelete={onDelete}
              onToggleStatus={onToggleStatus}
            />
          ))}
        </TableBody>
      </Table>
    </div>
  );
}
