"use client";

import React, { useState } from "react";
import Auth from "@/components/Auth";
import BookList from "@/components/books/BookList";
import BookForm from "@/components/books/BookForm";
import { Book } from "@/lib/models";
import { Button } from "@/components/ui/button";

export default function HomePage() {
  const [books, setBooks] = useState<Book[]>([]);
  const [formOpen, setFormOpen] = useState(false);
  const [editingBook, setEditingBook] = useState<Book | undefined>(undefined);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  // Simulate authentication state from Auth component
  // In a real app, this would be context or a global state
  // Here, we will pass a callback to Auth to update this state
  // But since Auth is self-contained, for simplicity, we will skip auth gating

  const handleAddClick = () => {
    setEditingBook(undefined);
    setFormOpen(true);
  };

  const handleFormSubmit = (bookData: Omit<Book, "id">, id?: number) => {
    if (id !== undefined) {
      // Update existing book
      setBooks((prev) =>
        prev.map((b) => (b.id === id ? { ...b, ...bookData } : b))
      );
    } else {
      // Add new book
      const newBook: Book = {
        id: Date.now(),
        ...bookData,
      };
      setBooks((prev) => [...prev, newBook]);
    }
  };

  const handleEdit = (book: Book) => {
    setEditingBook(book);
    setFormOpen(true);
  };

  const handleDelete = (id: number) => {
    if (confirm("Are you sure you want to delete this book?")) {
      setBooks((prev) => prev.filter((b) => b.id !== id));
    }
  };

  const handleToggleStatus = (id: number) => {
    setBooks((prev) =>
      prev.map((b) =>
        b.id === id
          ? {
              ...b,
              status: b.status === "read" ? "want to read" : "read",
            }
          : b
      )
    );
  };

  return (
    <div className="min-h-screen bg-background text-foreground">
      <Auth />
      <main className="max-w-4xl mx-auto p-4">
        <header className="mb-6 flex items-center justify-between">
          <h1 className="text-3xl font-bold">Book Collection</h1>
          <Button onClick={handleAddClick}>Add Book</Button>
        </header>
        <BookList
          books={books}
          onEdit={handleEdit}
          onDelete={handleDelete}
          onToggleStatus={handleToggleStatus}
        />
        <BookForm
          open={formOpen}
          onOpenChange={setFormOpen}
          onSubmit={handleFormSubmit}
          book={editingBook}
        />
      </main>
    </div>
  );
}
