"use client";

import React from "react";
import { Book } from "@/lib/models";
import { TableRow, TableCell } from "@/components/ui/table";
import { Button } from "@/components/ui/button";

interface BookItemProps {
  book: Book;
  onEdit: (book: Book) => void;
  onDelete: (id: number) => void;
  onToggleStatus: (id: number) => void;
}

export default function BookItem({ book, onEdit, onDelete, onToggleStatus }: BookItemProps) {
  return (
    <TableRow>
      <TableCell>{book.title}</TableCell>
      <TableCell>{book.author}</TableCell>
      <TableCell>{book.genre}</TableCell>
      <TableCell>{book.rating}</TableCell>
      <TableCell>
        <Button variant="outline" size="sm" onClick={() => onToggleStatus(book.id)}>
          {book.status === "read" ? "Mark as Want to Read" : "Mark as Read"}
        </Button>
      </TableCell>
      <TableCell>
        <Button variant="ghost" size="sm" onClick={() => onEdit(book)}>
          Edit
        </Button>
        <Button variant="destructive" size="sm" onClick={() => onDelete(book.id)} className="ml-2">
          Delete
        </Button>
      </TableCell>
    </TableRow>
  );
}
